@import url(https://fonts.googleapis.com/css?family=Bowlby+One+SC);
@import url(https://fonts.googleapis.com/css?family=Bowlby+One+SC);
.navbar{background-color:#a11919;border:0.3em solid yellow;font-family:Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;font-style:oblique;box-shadow:0px 16px 10px 0px #0000003d;margin:0.3em 0em 0.8em 0em}#nb-title{color:white;text-align:center;font-family:"Bowlby One SC";width:100vw;font-size:7.5vw;text-decoration:none}

img{height:42vh}


.selectedMovie{display:flex;flex-direction:row;justify-content:space-around;background-color:gainsboro;padding:5px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.selectedMovie img{width:45vw;height:100vh;border-radius:15%;box-shadow:0px 16px 10px 0px #0000003d}.movie-info{display:flex;width:50vw;flex-direction:column;justify-content:space-between;align-self:center;background:linear-gradient(#e66465, #9198e5);border-radius:5%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:2em;font-family:Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif}.movie-info h1{font-size:5.5vw;background-color:yellow;border:0.2em solid #a11919;box-shadow:0px 16px 10px 0px #0000003d;text-align:center;font-family:"Bowlby One SC", cursive;width:auto;margin-bottom:0.2em}.movie-info h2{text-align:center;margin-bottom:0.4em}.movie-info p,.movie-info ul{background-color:rgba(161,25,25,0.644);font-family:"Segoe UI", Tahoma, Geneva, Verdana, sans-serif;color:white;border:0.2em solid yellow;padding:10px;list-style:none}.movie-info u{font-size:1.1vw;font-family:"Bowlby One SC", cursive}

.App{text-align:center}.no-rating{padding-top:40px;text-align:center;color:white}.no-rating img{height:30vh;margin-bottom:10vh}

.card{width:15rem;cursor:pointer;transition:all 0.2s ease-in-out;margin:0.2em}.card-img-overlay{opacity:0}.card:hover>.card-img-overlay{opacity:1 !important}.card:hover>.card-img{opacity:0.2}.card:hover{cursor:pointer;transform:scale(1.025);z-index:0}



.dropdown{padding-right:8px;padding-top:5px}.dropdown button{float:right;background-color:#79838d}

body {
  background: linear-gradient(#434343, #000000);
}

