@import url("https://fonts.googleapis.com/css?family=Bowlby+One+SC");
.selectedMovie {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: gainsboro;
  padding: 5px;
  height: fit-content;
  img {
    width: 45vw;
    height: 100vh;
    border-radius: 15%;
    box-shadow: 0px 16px 10px 0px #0000003d;
  }
}

.movie-info {
  display: flex;
  width: 50vw;
  flex-direction: column;
  justify-content: space-between;
  align-self: center;
  background: linear-gradient(#e66465, #9198e5);
  border-radius: 5%;
  height: fit-content;
  padding: 2em;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  h1 {
    font-size: 5.5vw;
    background-color: yellow;
    border: 0.2em solid rgb(161, 25, 25);
    box-shadow: 0px 16px 10px 0px #0000003d;
    text-align: center;
    font-family: "Bowlby One SC", cursive;
    width: auto;
    margin-bottom: 0.2em;
  }
  h2 {
    text-align: center;
    margin-bottom: 0.4em;
  }
  p,
  ul {
    background-color: rgba(161, 25, 25, 0.644);
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    color: white;
    border: 0.2em solid yellow;
    padding: 10px;
    list-style: none;
  }
  u {
    font-size: 1.1vw;
    font-family: "Bowlby One SC", cursive;
  }
}
